* {
  box-sizing: border-box;
}

body {
  color: #060b35;
  scroll-behavior: smooth;
  margin: 0;
  font-family: Roboto, sans-serif;
}

ul, li, a, p {
  color: #000;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 0 50px;
  font-family: Roboto, serif;
  font-style: normal;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__logo-link {
  height: 40px;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 12px;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  height: 60px;
  line-height: 60px;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
  cursor: pointer;
  transition: color .3s;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card__describe {
  text-align: left;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transition: transform .3s;
  transform: scale(1.2);
}

.card:hover .card__describe {
  color: #34568b;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  margin-top: 2px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__amount {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(n) {
  background-image: url("star-active.edda4c43.svg");
}

.button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 38px;
  transition: color .3s, background-color .3s;
  display: block;
}

.button:hover, .button:focus {
  color: #00acdc;
  background-color: #fff;
  transition: color .3s, background-color .3s;
}

main {
  --number-of-columns: 1;
  grid-template-columns: repeat(var(--number-of-columns), 200px);
  grid-gap: 46px 48px;
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  main {
    --number-of-columns: 2;
  }
}

@media (width >= 768px) {
  main {
    --number-of-columns: 3;
  }
}

@media (width >= 1024px) {
  main {
    --number-of-columns: 4;
  }
}
